}

.advice__list {
  grid-gap: 16px;
  grid-template-rows: repeat(6, 192px);
  grid-template-columns: 1fr;
  grid-template-areas: "pic1"
                       "pic2"
                       "pic3"
                       "pic4"
                       "pic5"
                       "pic6";
  display: grid;
}

