    grid-gap: 24px 24px;
    grid-template-rows: repeat(3, 292px);
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "pic1 pic2"
                         "pic3 pic4"
                         "pic5 pic6";
    display: grid;
  }
}

@media screen and (min-width: 1560px) {
