  position: relative;
  overflow: hidden;
}

.advice__item--grid1 {
  grid-area: pic1;
}

.advice__item--grid2 {
  grid-area: pic2;
}

.advice__item--grid3 {
  grid-area: pic3;
}

.advice__item--grid4 {
  grid-area: pic4;
}

.advice__item--grid5 {
  grid-area: pic5;
}

.advice__item--grid6 {
